.LoginSignUpContainer {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  overflow: hidden;
}

.LoginSignUpBox {

  -webkit-backdrop-filter: blur(5px);

          backdrop-filter: blur(5px);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.8);
  width: 27vmax;
  height: 40vmax;
  box-sizing: border-box;
  overflow: hidden;
 
}
.login_signUp_toggle {
  display: flex;
  height: 3vmax;
}
.login_signUp_toggle > p {
  color: #ff007a;
  font: 300 1vmax "Nunito";
  transition: all 0.5s;
  cursor: pointer;
  display: grid;
  place-items: center;
  width: 100%;
}
.login_signUp_toggle > p:hover {
  color: black;
}

.LoginSignUpBox > div > button {
  background-color: #ff007a;
  height: 3px;
  width: 50%;
  border: none;

  transition: all 0.5s;
}

.loginForm,
.signUpForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  padding: 2vmax;
  justify-content: space-evenly;
  height: 70%;
  transition: all 0.5s;
}

.signUpForm {
  -webkit-transform: translateY(-100%) translateX(-100vmax);
          transform: translateY(-100%) translateX(-100vmax);
}

.loginForm > div,
.signUpForm > div {
  display: flex;
  width: 100%;
  align-items: center;
}
.loginForm > div > input,
.signUpForm > div > input {
  background: transparent;
  padding: 1vmax 4vmax;
  padding-right: 1vmax;
  width: 100%;
  box-sizing: border-box;
  border: none ;
  font: 300 1.2vmax 'Nunito';
  outline: none;
  color: black;

}
.forgot{
  color: white;
}

.loginForm > div > svg,
.signUpForm > div > svg {
  position: absolute;
  -webkit-transform: translateX(1vmax);
          transform: translateX(1vmax);
  font-size: 1.6vmax;
  color: #ff007a;
  z-index: 10;
}

.loginForm > p {
  color: black;
  text-decoration: none;
  align-self: flex-end;
  transition: all 0.5s;
  font: 500 1vmax "Gill Sans";
  cursor: pointer;
}

.loginForm > a:hover {
  color: black;
}
#registerImage {
    position: relative;
    overflow: hidden;
}
#registerImage > img {
    position: absolute;
    top:5%;
    left:3%;
  width: 2.8vmax;
  height: 2.8vmax;
  border-radius: 50%;
 
}
#registerImage > input {
  display: flex;
  padding: 0%;
}

#registerImage > input::file-selector-button {
  cursor: pointer;
  width: 100%;
  z-index: 2;
  height: 3vmax;
  border: none;
  margin: 0%;
  font: 400 1vmax 'Nunito';
  transition: all 0.5s;
  padding: 0 1vmax;
  color: #ff007a;
  text-align: left;
  padding-left: 4vmax;
  background: transparent;
}

#registerImage > input::file-selector-button:hover {
  background-color: rgba(0, 0, 0, 0.185);
}

.loginBtn,
.signUpBtn {
  border: none;
  background-color: #ff007a;
  color: black;
  font: 300 0.9vmax "Nunito";
  width: 100%;
  padding: 0.8vmax;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
  font-size: 1.2vmax;
  -webkit-transform: translateY(3vmax);
          transform: translateY(3vmax);
}

.loginBtn:hover,
.signUpBtn:hover {
  background-color: black;
  color: white;
}

.shiftToLeft {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.shiftToNeutral {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.shiftToNeutralForm {
  -webkit-transform: translateX(0%) translateY(-100%);
          transform: translateX(0%) translateY(-100%);
}

.shiftToRight {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

