    box-sizing: border-box;
    overflow: hidden;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.8);
  }
  
  .resetPasswordHeading {
    text-align: center;
    color: white;
    font: 400 1.3vmax "Nunito";
    padding: 1.3vmax;
    border-bottom: 1px solid rgb(0, 255, 191);
    width: 50%;
    margin: auto;
  }
  
  .resetPasswordForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 70%;
    transition: all 0.5s;
  }
  
  .resetPasswordForm > div {
    display: flex;
    width: 100%;
    align-items: center;
  }
  
  .resetPasswordForm > div > input {
    padding: 1vmax 4vmax;

  }
  
  .resetPasswordForm > div > svg {
    position: absolute;
    -webkit-transform: translateX(1vmax);
            transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgb(0, 255, 191);
  }
  
  .resetPasswordBtn {
    border: none;
    background-color: rgb(0, 255, 191);
    color: white;
    font: 300 0.9vmax "Nunito";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
  }
  
  .resetPasswordBtn:hover {
    background-color: black;
  }
  
  @media screen and (max-width: 640px) {
    .resetPasswordContainer {
      background-color: white;
    }
    .resetPasswordBox {
      width: 100vw;
      height: 95vh;
      margin-top: 10vmax;
      display: flex;
      flex-direction: column;
    }
    .resetPasswordHeading {
        font: 400 2.5vmax "Nunito";
        margin-bottom:0 ;
      }
      
  
    .resetPasswordForm {
      padding: 0vmax;
      margin-top: 0;
    }
  
    .resetPasswordForm > div > input {
      padding: 2.5vmax 5vmax;
      font: 300 1.7vmax 'Nunito';
    }
  
    .resetPasswordForm > div > svg {
      font-size: 2.8vmax;
    }
  
    .resetPasswordBtn {
      font: 300 1.9vmax "Nunito";
